
const IS_BROWSER = typeof window !== 'undefined' && window.name !== 'nodejs';
const IS_REACT18 = process.env.IS_REACT18 === 'true';
const MOUNT_ID = 'root';

import React from 'react';

import ReactDOM from 'react-dom/client';

import { createApp, bootstrap } from '@edenx/runtime';

import App from '@_edenx_src/routes/clasi/page.tsx';

import { router } from '@edenx/runtime/plugins';


let AppWrapper = null;

let root = null;

function render() {
  
  const finalAppConfig = {
    ...App.config,
    ...typeof config === 'function' ? config() : {},
  }

  AppWrapper = createApp({
    plugins: [
     router({...{"serverBase":["/clasi"]}, ...finalAppConfig?.router}),
       
    ]
  })(App)


  if(!AppWrapper.init && typeof appInit !== 'undefined') {
    AppWrapper.init = appInit;
  }


  if (IS_BROWSER) {
    bootstrap(AppWrapper, MOUNT_ID, root, ReactDOM);
  }

  return AppWrapper

}

AppWrapper = render();

export default AppWrapper;;
